import { SelectPrimitive } from '@vanilla-primitives/core';
export class SelectTrigger extends SelectPrimitive.Trigger {
    connectedCallback() {
        this.className = 'select--trigger';

        const iconSvg = `
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="lucide lucide-chevron-down" aria-hidden="true">
                <path d="m6 9 6 6 6-6"></path>
            </svg>
        `;

        this.insertAdjacentHTML('beforeend', iconSvg);

        super.connectedCallback();
    }
}

export class SelectContent extends SelectPrimitive.Content {
    connectedCallback() {
        this.className = 'select--content';
        super.connectedCallback();
    }
}

export class SelectLabel extends SelectPrimitive.Label {
    connectedCallback() {
        this.className = 'select--label';
        super.connectedCallback();
    }
}

export class SelectItem extends SelectPrimitive.Item {
    connectedCallback() {
        const originalContent = this.innerHTML;
        this.innerHTML = `
        <span>
        <svg slot="indicator" class="select--item-indicator" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="lucide lucide-check h-4 w-4"><path d="M20 6 9 17l-5-5"></path></svg>
            </span>
            <span slot="item-text">${originalContent}</span>
        `;

        this.className = 'select--item';

        super.connectedCallback();
    }
}

export const Select = SelectPrimitive.Root;
export const SelectGroup = SelectPrimitive.Group;
export const SelectValue = SelectPrimitive.Value;